n    &::after {\n      width: 1px;\n      height: 100%;\n    }\n\n    &::before {\n      left: 33.3333%;\n      left: calc(100% / 3);\n    }\n\n    &::after {\n      left: 66.6666%;\n      left: calc(100% / 3 * 2);\n    }\n  }\n\n  &__rule-of-thirds-hz {\n    &::before,\n    &::after {\n      width: 100%;\n      height: 1px;\n    }\n\n    &::before {\n      top: 33.3333%;\n      top: calc(100% / 3);\n    }\n\n    &::after {\n      top: 66.6666%;\n      top: calc(100% / 3 * 2);\n    }\n  }\n\n  &__drag-handle {\n    position: absolute;\n    width: var(--rc-drag-handle-size);\n    height: var(--rc-drag-handle-size);\n    background-color: var(--rc-drag-handle-bg-colour);\n    border: 1px solid var(--rc-border-color);\n\n    &:focus {\n      background: var(--rc-focus-color);\n    }\n  }\n\n  .ord-nw {\n    top: 0;\n    left: 0;\n    transform: translate(-50%, -50%);\n    cursor: nw-resize;\n  }\n  .ord-n {\n    top: 0;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    cursor: n-resize;\n  }\n  .ord-ne {\n    top: 0;\n    right: 0;\n    transform: translate(50%, -50%);\n    cursor: ne-resize;\n  }\n  .ord-e {\n    top: 50%;\n    right: 0;\n    transform: translate(50%, -50%);\n    cursor: e-resize;\n  }\n  .ord-se {\n    bottom: 0;\n    right: 0;\n    transform: translate(50%, 50%);\n    cursor: se-resize;\n  }\n  .ord-s {\n    bottom: 0;\n    left: 50%;\n    transform: translate(-50%, 50%);\n    cursor: s-resize;\n  }\n  .ord-sw {\n    bottom: 0;\n    left: 0;\n    transform: translate(-50%, 50%);\n    cursor: sw-resize;\n  }\n  .ord-w {\n    top: 50%;\n    left: 0;\n    transform: translate(-50%, -50%);\n    cursor: w-resize;\n  }\n\n  // Use the same specificity as the ords above but just\n  // come after.\n  &__disabled &__drag-handle {\n    cursor: inherit;\n  }\n\n  &__drag-bar {\n    position: absolute;\n\n    &.ord-n {\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: var(--rc-drag-bar-size);\n      transform: translateY(-50%);\n    }\